body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@font-face {
  font-family: "CheeseGraterRegular";
  src: local("CheeseGraterRegular"),
   url("./fonts/CheeseGraterRegular-DOn4d.ttf") format("truetype");
  font-weight: bold;
  }

@font-face {
  font-family: "FrankRuhlLibre-Medium";
  src: local("FrankRuhlLibre-Medium"),
  url("./fonts/FrankRuhlLibre-Medium.ttf") format("truetype");
  font-weight: bold;
  }


@font-face {
  font-family: "GreatVibes-Regular";
  src: local("GreatVibes-Regular"),
  url("./fonts/GreatVibes-Regular.ttf") format("truetype");
  font-weight: bold;
  }

